/* styles go here */
body {
  background-color: lightblue;
}

fieldset {
  background-color: #5f9ea0;
  margin: auto;
  max-width: 20%;
}

.first_section,
.second_section,
.third_section {
  display: flex;
}

legend {
  position: relative;
  bottom: 8px;
  font-weight: bold;
}

.register label {
  display: block;
  margin-bottom: 13px;
}

.sign_text label:not(.remember) {
  display: block;
  margin-bottom: 13px;
}

.message {
  display: block;
  position: relative;
  left: 40%;
  margin: 30px auto;
  color: brown;
}

.first_block {
  display: flex;
}

.second_block {
  display: flex;
}

input,
select {
  margin-bottom: 10px;
  display: block;
}

.question {
  display: inline-block;
  width: 100px;
}

.second_text {
  display: inline-block;
  width: 100px;
}

.question_1 {
  margin: 0;
}

.question_1 input {
  display: inline;
}

.question_1 textarea {
  display: block;
}

.first_question {
  border: 1px solid RGB(72,61,89);
  border-radius: 8px;
  padding: 2px;
}

.input_range {
  padding: 20px;
  margin: 15px;
}

.input_time {
  margin: 50px 30px;
}

.input_color {
  margin-top: 50px;
  margin-left: 30px;
}

.input_terms {
  margin: 35px;
}

.sign_input,
.first_input,
.second_input {
  margin-left: 30px;
}

.button_place {
  display: flex;
  margin-top: 30px;
  margin-bottom: 20px;
  margin-left: 43%;

}

.button_place button {
  display: block;
  font-size: medium;
}

[type="reset"] {
  margin-left: 2%;
}
